<template>
  <mu-col span="3" xl="3" >
      <mu-button @click="addProject">Создать проект</mu-button>
      <div v-for="room in rooms">
        <h3 @click="openWorkspace(room.id)">{{room.creator.username}} | {{room.id}}</h3>
        <small>{{room.date}}</small>
      </div>
  </mu-col>
</template>

<script>
  import $ from 'jQuery'

  export default {
    name: 'ProjectRoom',
    data() {
      return {
        rooms: '',
      }  
    },
    created() {
      $.ajaxSetup({
        headers: { 'Authorization': "Token " + localStorage.getItem('auth_token') },
      });
      this.loadRoom()
    },
    methods: {
      loadRoom() {
        $.ajax({
          url: "https://satellite-django.herokuapp.com/api/workspace/room/",
          type: "GET",
          success: (response) => {
            this.rooms = response.data.data
          }
        })
      },
      openWorkspace(id) {
        this.$emit("openWorkspace", id)
      },
      addProject() {
        $.ajax({
          url: "https://satellite-django.herokuapp.com/api/workspace/room/",
          type: "POST",
          success: (response) => {
            this.loadRoom()
          }

        })
      }
    }
  }
</script>

<style>
  h3 {
    cursor: pointer;
  }
</style>



