<template>
  <div class="form">
    <mu-container>
      <mu-appbar style="width: 100%" color="black" class="bar">
        Обработчик спутниковых данных
      </mu-appbar>
      <mu-col sm="8">
        <mu-form ref="form" :model="validateForm" class="mu-demo-form">
          <mu-form-item label="Email" prop="email">
            <mu-text-field type="text" v-model="validateForm.email" prop="email"></mu-text-field>
          </mu-form-item>
          <mu-form-item label="username" prop="username" :rules="usernameRules">
            <mu-text-field v-model="validateForm.username" prop="username"></mu-text-field>
          </mu-form-item>
          <mu-form-item label="Пароль" prop="password" :rules="passwordRules">
            <mu-text-field type="password" v-model="validateForm.password" prop="password"></mu-text-field>
          </mu-form-item>
          <mu-form-item>
            <mu-button color="primary" @click="setLogin">Зарегистрироваться</mu-button>
            <mu-button @click="goLogin">Войти</mu-button>
            <mu-button @click="clear">Сбросить</mu-button>
          </mu-form-item>
        </mu-form>
      </mu-col>
    </mu-container>
  </div>
</template>

<script>
  import $ from 'jQuery'
  export default {
    name: 'NewUser',
    data() {
      return {
        usernameRules: [
          { validate: (val) => !!val, message: 'Обязательное поле' },
          { validate: (val) => val.length >= 3, message: 'Ваш логин не может быть короче 3 символов' }
        ],
        passwordRules: [
          { validate: (val) => !!val, message: 'Обязательное поле' },
          { validate: (val) => val.length >= 10, message: 'Пароль должен быть не короче 10 символов а также содержать в себе буквы латинского алфавита' },
        ],
        validateForm: {
          username: '',
          password: '',
          email: '',
        }
      }
    },
    methods: {
      clear() {
        this.$refs.form.clear();
        this.validateForm = {
          username: '',
          password: '',
          isAgree: false
        };
      },
      goLogin() {
        this.$router.push({ name: "Login" })
      },
      setLogin() {
        $.ajax({
          url: "https://satellite-django.herokuapp.com/auth/users/",
          type: "POST",
          data: {
            username: this.validateForm.username,
            password: this.validateForm.password,
            email: this.validateForm.email
          },
          success: (response) => {
            console.log(response.data.attributes.auth_token)
            alert("Спасибо за регистрацию")
            localStorage.setItem("auth_token", response.data.attributes.auth_token)
            this.$router.push({ name: "Login" })
          },
          error: (response) => {
            if (response.status === 400) {
              alert("Проверь логин или пароль")
            }  
          }
        })
      },
    }
  }
</script>

<style>
  .mu-demo-form {
    border: 1px solid #CCCCCC;
    border-radius: 10px;
    padding: 60px;
    margin: 30% auto -10% 26%;
    max-width: 900px;
  }


  .bar {
    position: fixed;
    left: 0%;
    top: 0%;
  }
</style>



