<template>
  <mu-col>
    <mu-container class="workspace">
      <mu-row v-for="workspace in workspaces"
              :key="workspace.id">
      <Map :map_data="workspace.map"></Map>
      </mu-row>
    </mu-container>
    <mu-container class="message" id="app">
      <mu-row>
        <label>
          <input id="uploadimage" type="FILE" name="uploadimage" class="upload">
        </label>
        <mu-col span="12" lg="4" sm="6">
        </mu-col>
        <mu-button round color="primary" @click="Submit" class="button">Загрузить данные</mu-button>
      </mu-row>
    </mu-container>
  </mu-col>
</template>

<script>
  import $ from 'jQuery'
  import Map from '@/components/Map'

  export default {
    name: 'Workspace',
    components: {
      Map
    },
    props: {
      id: '',
    },
    data() {
      return {
        workspaces: '',
        textarea: '',
        file: '',
   
      }
    },
    created() {
      $.ajaxSetup({
        headers: { 'Authorization': "Token " + localStorage.getItem('auth_token') },
      });
      this.loadWorkspace()
      setInterval(() => {
        this.loadWorkspace()
      }, 5000)
    },
    methods: {
      loadWorkspace() {
        $.ajax({
          url: "https://satellite-django.herokuapp.com/api/workspace/project/",
          type: "GET",
          data: {
            room: this.id
          },
          success: (response) => {
            this.workspaces = response.data.data
          }
        })
      },
      handleFileUpload() {
        this.file = this.$refs.file.files[0];
      },
      Submit() {
        var formData = new FormData()
        var $input = $("#uploadimage");
        formData.append('map', $input.prop('files')[0])
        formData.append('text', this.textarea)
        formData.append('room', this.id)
        $.ajax({
          url: "https://satellite-django.herokuapp.com/api/workspace/project/",
          type: "POST",
          processData: false,
          contentType: false,
          data: formData,
          success: (response) => {
            this.loadWorkspace()
          },
          error: (response) => {
            alert("Вы не можете отобразить пустые данные а также загружать их повторно")
          }
        }),
          this.$refs["map"].value = ""
      },
    }
  }

</script>

<style scoped>

  .workspace {
    position: fixed;
    overflow: auto;
    max-height: 100%;
    min-height: 0px;
    width: 100%;
    top: 15%;
    background-color: white;
    border-radius: 10px;
  }
  .map{
      margin-left: 30%
  }
  .message {
    background-color: white;
    position: fixed;
    bottom: -12%;
    width: 100%;
    height: 15%;
    margin: 5% 0% 4% 2%;
    border-radius: 10px;
  }
  .button{
      margin: 2% 12%;
  }
  .upload{
      margin: 10% 45%;
  }
  
</style>



