<template>
  <div class="hello">
    <!--<canvas id="plot"></canvas>-->
    <div id="mapContainer"></div>
  </div>
</template>

<script>
  import "leaflet/dist/leaflet.css";
  import L from "leaflet"
  import "leaflet-geotiff-2";
  import * as GeoTIFF from 'geotiff/dist-browser/geotiff';
  import * as d3 from 'd3'
  import * as plotty from 'plotty'
  import "leaflet-geotiff-2/dist/leaflet-geotiff-plotty";
  import "leaflet-geotiff-2/dist/leaflet-geotiff-rgb";
  import "georaster-layer-for-leaflet"

  export default {
    name: 'Map',

    props: {
      map_data: {
        type: String,
        default() {
          return {}
        }
      }
    },
    data() {
      return {

      }
    },
    methods: {
      setupLeafletMap: function () {
        var parse_georaster = require("georaster");

        var GeoRasterLayer = require("georaster-layer-for-leaflet");
        var mapDiv = L.map("mapContainer").setView([0, 150], 2);
        L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
          attribution: '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors',
        }).addTo(mapDiv);

        const url_to_geotiff_file = "https://satellite-django.herokuapp.com" + this.map_data;

        fetch(url_to_geotiff_file)
          .then(response => response.arrayBuffer())
          .then(arrayBuffer => {
            parse_georaster(arrayBuffer).then(georaster => {
              console.log("georaster:", georaster);

              var layer = new GeoRasterLayer({
                georaster: georaster,
                opacity: 0.5,
                pixelValuesToColorFn: values => values[0] > 100 ? '#999999' : '#FFFFFF',
                resolution: 1024 // optional parameter for adjusting display resolution
              });
              layer.addTo(mapDiv);

              map.fitBounds(layer.getBounds());

            });
          });
      },
    },
    mounted() {

      this.setupLeafletMap();


      //var someUrl = 'http://127.0.0.1:8000' + this.map_data
      //const canvas = document.getElementById("plot");
      //GeoTIFF.fromUrl(someUrl)
      //  .then(tiff => {
      //    tiff.getImage().then(image => {
      //      console.log(image)
      //      image.readRasters({ resampleMethod: 'bilinear' }).then(data => {
      //        const plot = new plotty.plot({
      //          layer,
      //          canvas,
      //          data: data[0],
      //          width: image.getWidth(),
       ///         height: image.getHeight(),
      //          domain: [0, 1],
      //          noDataValue: -3.40282346639e+038,
      //          colorScale: "viridis"
      //        });
      //        plot.render();
      //      })
      //    })
       // });
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1, h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }
  #mapContainer {
    width: 80vw;
    height: 100vh;
  }
</style>



