<template class="main">
  <mu-container class="main">
    <mu-appbar style="width: 100%" color="black" class="bar">
      Обработчик спутниковых данных
    </mu-appbar>
    <mu-col sm="8">
      <mu-form ref="form" :model="validateForm" class="mu-demo-form">
        <mu-form-item label="Логин" prop="username">
          <mu-text-field v-model="validateForm.username" prop="username"></mu-text-field>
        </mu-form-item>
        <mu-form-item label="Пароль" prop="password">
          <mu-text-field type="password" v-model="validateForm.password" prop="password"></mu-text-field>
        </mu-form-item>
        <mu-form-item>
          <mu-button color="primary" @click="setLogin">Войти</mu-button>
          <mu-button @click="goCreate">Зарегистрироваться</mu-button>
          <mu-button @click="clear">Сбросить</mu-button>
        </mu-form-item>
      </mu-form>
    </mu-col>
  </mu-container>

</template>

<script>
  import $ from 'jQuery'
  export default {
    name: 'Login',
    data() {
      return {
        validateForm: {
          username: '',
          password: '',
        }
      }
    },
    methods: {
      goCreate() {
        this.$router.push({ name: "NewUser" })
      },
      submit() {
        this.$refs.form.validate().then((result) => {
          console.log('form valid: ', result)
        });
      },
      clear() {
        this.$refs.form.clear();
        this.validateForm = {
          username: '',
          password: '',
          isAgree: false
        };
      },
      setLogin() {
        $.ajax({
          url: "https://satellite-django.herokuapp.com/auth/token/login",
          type: "POST",
          data: {
            username: this.validateForm.username,
            password: this.validateForm.password
          },
          success: (response) => {
            console.log(response.data.attributes.auth_token)
            alert("Добро пожаловать")
            localStorage.setItem("auth_token", response.data.attributes.auth_token)
            this.$router.push({ name: "Home" })
          },
          error: (response) => {
            if (response.status === 400) {
              alert("перепроверь логин или пароль")
            }  
          }
        })
      },
    }
  }
</script>

<style>
  .mu-demo-form {
    border: 1px solid #CCCCCC;
    border-radius: 10px;
    padding: 60px;
    margin: 30% auto -10% 26%;
    max-width: 900px;
  }

  .bar {
    position: fixed;
    left: 0%;
    top: 0%;
  }
</style>



