<template class="main">
  <mu-container>
    <mu-appbar style="width: 100%" color="black" class="bar">
      Обработчик спутниковых данных
      <mu-button flat slot="right" v-if="!auth" @click="goLogin">Вход</mu-button>
      <mu-button flat slot="right" v-if="!auth" @click="goCreate">Зарегистрироваться</mu-button>
      <mu-button flat slot="right" v-else="" @click="goLogout">Выйти</mu-button>
    </mu-appbar>
    <mu-row>
      <mu-row>
        <h1 v-if="!auth">Для того, чтобы пользоваться сервисом необходимо войти или зарегистрироваться</h1>
        <h1 v-else></h1>
        <img src=""/>
      </mu-row>
    </mu-row>
    <mu-col class="projects">
      <ProjectRoom v-if="auth" @openWorkspace="openWorkspace"></ProjectRoom>
    </mu-col>
    <mu-col class="workspace">
      <Workspace v-if="workspace.show" :id="workspace.id"></Workspace>
    </mu-col>
  </mu-container>
</template>

<script>
  import ProjectRoom from '@/components/rooms/ProjectRoom.vue'
  import Workspace from '@/components/rooms/Workspace.vue'

  export default {
    name: 'Home',
    components: {
      ProjectRoom,
      Workspace,
      Map,
    },
    data() {
      return {
        workspace: {
          id: '',
          show: false,
        }
      }  
    },
    computed: {
      auth() {
        if (localStorage.getItem("auth_token")) {
          return true
        }
      }
    },
    methods: {
      goLogin() {
        this.$router.push({name: "Login"})
      },
      goCreate() {
        this.$router.push({ name: "NewUser" })
      },
      goLogout() {
        localStorage.removeItem("auth_token")
        window.location = '/'
      },
      openWorkspace(id) {
        this.workspace.id = id
        this.workspace.show = true
      },
    },
  }
</script>

<style>
  .bar {
    position: fixed;
    left: 0%;
    top: 0%;
  }
  .projects{
      position: absolute;
      margin: 2% auto 0% -22%;
  }
  .workspace{
    position: fixed;
    margin: -3.5% 10% 0% 2%;
  }
</style>



